import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";


// data.stats     
interface Stat {
  base_stat: number;
}

// data.types     
interface TypeInfo {
  type: {
    name: string;
  };
}

// data.abilities     
interface AbilityInfo {
  ability: {
    name: string;
  };
}

// data    
interface PokemonData {
  id: number;
  name: string;
  sprites: {
    front_default: string;
  };
  types: TypeInfo[];
  stats: Stat[];
  height: number;  // cm 
  weight: number;  // hectograms (100g )
  abilities: AbilityInfo[];
}

// pokemonInfo  
interface PokemonInfo {
  id: number;
  name: string;
  sprite: string;
  types: string[];
  stats: {
    hp: number;
    attack: number;
    defense: number;
    specialAttack: number;
    specialDefense: number;
    speed: number;
  };
  height: number;  //  
  weight: number;  // kg 
  abilities: string[];
}

const fetchPokemonById = createAsyncThunk(
  "pokemon/fetchByName",
  async (poketmonId: string) => {
    try {
      const response = await fetch(
        `https://pokeapi.co/api/v2/pokemon/${poketmonId}`
      );

      const data = await response.json();

      //    
      const pokemonInfo: PokemonInfo = {
        id: data.id,
        name: data.name,
        sprite: data.sprites.front_default,
        types: data.types.map((typeInfo: any) => typeInfo.type.name),
        stats: {
          hp: data.stats[0].base_stat,
          attack: data.stats[1].base_stat,
          defense: data.stats[2].base_stat,
          specialAttack: data.stats[3].base_stat,
          specialDefense: data.stats[4].base_stat,
          speed: data.stats[5].base_stat,
        },
        height: data.height / 10, //   
        weight: data.weight / 10, // kg  
        abilities: data.abilities.map((ability: any) => ability.ability.name),
      };

      return pokemonInfo;
    } catch (error) {
      console.error("Error fetching data:", error);
      throw new Error("Failed to fetch pokemon data");
    }
  }
);

const pokemonDetailSlice = createSlice({
  name: "pokemonDetail",
  initialState: {
    value: {
      id: 0,
      name: "",
      sprite: "",
      types: [],
      stats: {
        hp: 0,
        attack: 0,
        defense: 0,
        specialAttack: 0,
        specialDefense: 0,
        speed: 0,
      },
      height: 0,
      weight: 0,
      abilities: [],
    } as PokemonInfo, //   
    status: "Welcome",
  },
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(fetchPokemonById.pending, (state, action) => {
      state.status = "Loading";
    });
    builder.addCase(fetchPokemonById.fulfilled, (state, action) => {
      state.value = action.payload;
      state.status = "complete";
    });
    builder.addCase(fetchPokemonById.rejected, (state, action) => {
      state.status = "fail";
    });
  },
});

export default pokemonDetailSlice; // cardData slice export
export {fetchPokemonById};